import React, { Component } from 'react';
import { 
  Button,
  Card, 
  CardBody, 
  CardHeader, 
  Col, 
  Row, 
  Fade,
  Collapse,
  Input,
  FormGroup,
  Label
} from 'reactstrap';
import { history } from '../../helpers/history';
import { connect } from 'react-redux';
import { getDataById, editData } from '../../redux/category/categoryAction';

class Tables extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.toggleFade = this.toggleFade.bind(this);
        this.state = {
            collapse: true,
            fadeIn: true,
            timeout: 300,
            nama_category: '',
            data: [],
        };
        this.handleClick = this.handleClick.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
    }

    async componentWillMount(){
        const id = this.props.match.params.id;
        await this.props.dispatch(getDataById(id))
    }

    async componentWillReceiveProps(nextProps){
        const newData = await nextProps.datas.datas.datas;
        this.setState({
            data: newData,
            nama_category: newData ? newData[0].nama_category : null,
        })
    }

    handleClick = event => {
        event.preventDefault();
        const payload = {
            "nama_category": this.state.nama_category,
            "id": this.props.match.params.id
        }
        
        this.props.dispatch(editData(payload));
    };

    handleChange = prop => event => {
        this.setState({ [prop]: event.target.value });
    }

    handleCancel() {
        history.push('/data-user');
    }

    toggle() {
      this.setState({ collapse: !this.state.collapse });
    }

    toggleFade() {
      this.setState((prevState) => { return { fadeIn: !prevState }});
    }
  
    render() {
        // console.log(this.state.data)
        return (
          <div className="animated fadeIn">
            <Row>
              <Col xs="12">
                <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
                  <Card>
                    <CardHeader>
                      <i className="fa fa-edit"></i> Edit Category
                      <div className="card-header-actions">
                        <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                        <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle}><i className="icon-arrow-up"></i></Button>
                      </div>
                    </CardHeader>
                    <Collapse isOpen={this.state.collapse} id="collapseExample">
                      <CardBody>
                        <form onSubmit={this.handleClick} noValidate>
                            <div className="row">
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleEmail">Nama Category</Label>
                                      <Input 
                                        type="text" 
                                        name="nama_category" 
                                        onChange={this.handleChange('nama_category')} 
                                        value={this.state.nama_category || ""}
                                        placeholder="Nama" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12">
                                    <div style={{ float: 'right' }}>
                                        <button 
                                            type="submit" 
                                            className="btn btn-primary data-submit" 
                                        >
                                            Submit
                                        </button>
                                        &nbsp;
                                        <button
                                            className="btn btn-danger"
                                            onClick={this.handleCancel}
                                        >
                                            Cancel
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </form>
                      </CardBody>
                    </Collapse>
                  </Card>
                </Fade>
              </Col>
            </Row>
          </div>    
        );
    }
}

const mapStateToProps = (state) => {
    return {
        datas: state.dataCategory
  };
}

// const mapDispatchToProps = (dispatch) => {
//   return {
//     actions: bindActionCreators(createData, dispatch)
//   };
// }

export default connect(mapStateToProps)(Tables);